import React, { useEffect, useState } from 'react';
import { gapi } from 'gapi-script';
import { initClient, signIn, signOut, listFilesInFolder, getSheetData } from './googleAuth';
import LoadingScreen from './LoadingScreen';
import { AnimatePresence, motion } from 'framer-motion';
import './App.css';

const App = () => {
  const [isSignedIn, setIsSignedIn] = useState(false);
  const [isInitialized, setIsInitialized] = useState(false);
  const [sheetData, setSheetData] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredNames, setFilteredNames] = useState([]);
  const [selectedName, setSelectedName] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [date, setDate] = useState('');

  useEffect(() => {
    const initializeGapi = async () => {
      try {
        await initClient();
        const authInstance = gapi.auth2.getAuthInstance();
        if (authInstance) {
          setIsInitialized(true);
          const storedAuth = JSON.parse(localStorage.getItem('auth'));
          if (storedAuth && authInstance.isSignedIn.get()) {
            setIsSignedIn(true);
            fetchFiles();
          } else {
            setIsLoading(false);
          }
          authInstance.isSignedIn.listen(setIsSignedIn);
        } else {
          throw new Error('Google Auth instance not initialized');
        }
      } catch (error) {
        console.error('Error initializing Google API client:', error);
        setIsLoading(false);
      }
    };
    initializeGapi();
  }, []);

  useEffect(() => {
    if (isSignedIn) {
      fetchFiles();
    }
  }, [isSignedIn]);

  useEffect(() => {
    // Set a timer to update isLoading to false after 5 seconds
    const timer = setTimeout(() => {
      setIsLoading(false);
    }, 5000); // 5000 milliseconds = 5 seconds

    return () => clearTimeout(timer); // Cleanup the timer if the component unmounts
  }, []);

  const handleSignIn = async () => {
    if (!isInitialized) {
      console.error('Google Auth instance not initialized');
      return;
    }
    try {
      await signIn();
      setIsSignedIn(true);
      fetchFiles();
    } catch (error) {
      console.error('Error signing in:', error);
    }
  };

  const handleSignOut = async () => {
    if (!isInitialized) {
      console.error('Google Auth instance not initialized');
      return;
    }
    try {
      await signOut();
      setIsSignedIn(false);
      setSheetData([]);
      setSearchTerm('');
      setFilteredNames([]);
      setSelectedName(null);
      setIsLoading(true);
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  const fetchFiles = async () => {
    try {
      const fetchedFiles = await listFilesInFolder('June');

      // Get today's date in the format "DD-MM-YYYY"
      const today = new Date();
      const day = String(today.getDate()).padStart(2, '0');
      const month = String(today.getMonth() + 1).padStart(2, '0'); // January is 0
      const year = today.getFullYear();
      const todayFormatted = `${day}-${month}-${year}`;

      // Find the sheet that corresponds to today's date
      const todaySheet = fetchedFiles.find(file => file.name.includes(todayFormatted));
      if (todaySheet) {
        setDate(todaySheet.name.split(' ')[0]);
        const data = await getSheetData(todaySheet.id, todaySheet.name);
        if (data) {
          setSheetData(data.slice(2)); // Assuming the first two rows are headers
          setIsLoading(false);
        } else {
          setSheetData([]);
          setIsLoading(false);
          console.error('No data found for today\'s sheet');
        }
      } else {
        setSheetData([]);
        setIsLoading(false);
        console.error('No sheet found for today');
      }
    } catch (error) {
      setIsLoading(false);
      console.error('Error fetching files:', error);
    }
  };

  const handleSearch = (event) => {
    const term = event.target.value;
    setSearchTerm(term);
    if (term.length > 0) {
      const filtered = sheetData.filter(row => row[1] && row[1].toLowerCase().includes(term.toLowerCase()));
      setFilteredNames(filtered);
    } else {
      setFilteredNames([]);
    }
  };

  const handleSelectName = (name) => {
    setSelectedName(name);
    setSearchTerm('');
    setFilteredNames([]);
  };

  const getBgColor = (value) => {
    return value === 'OFF' ? 'bg-red-700' : 'bg-green-700';
  };

  return (
    <div className="flex flex-col h-screen">
      <AnimatePresence>
        {isLoading ? (
          <LoadingScreen />
        ) : (
          <motion.div
            className="container mx-auto  flex flex-col items-center flex-grow"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <h1 className="text-2xl text-white font-bold mb-4 bg-cyan-900 py-4 w-screen text-center shadow-md">Students Mess</h1>
            <h2 className="text-lg font-bold">Preferences for</h2>
            <h1 className="text-xl font-bold mb-4">Date: {date}</h1>
            {!isSignedIn ? (
              <button
                onClick={handleSignIn}
                className="bg-blue-500 text-white py-2 px-4 rounded flex"
              >
                Sign In
              </button>
            ) : (
              <div className='w-screen'>
                <div className="mt-4 ">
                  <div className="mb-4 mx-10">
                    <input
                      type="text"
                      className="border p-2 w-full"
                      placeholder="Search by name"
                      value={searchTerm}
                      onChange={handleSearch}
                    />
                    {filteredNames.length > 0 ? (
                      <ul className="border mt-2 max-h-48 overflow-y-auto">
                        {filteredNames.map((name, index) => (
                          <li
                            key={index}
                            className="p-2 cursor-pointer hover:bg-gray-200"
                            onClick={() => handleSelectName(name)}
                          >
                            {name[1]}
                          </li>
                        ))}
                      </ul>
                    ) : (
                      searchTerm && (
                        <div className="mt-10 text-center text-red-500 font-bold text-xl">
                          Preference Not filled
                        </div>
                      )
                    )}
                  </div>
                  {selectedName && (
                    <div className="mt-4 p-4 bg-white shadow-2xl rounded-md flex flex-col mx-10">
                      <h2 className="text-xl font-bold mb-4 text-center">{selectedName[1]}</h2>
                      <div className={`text-center py-2 rounded-md my-4 text-white ${getBgColor(selectedName[2])}`}>
                        <strong>Breakfast:</strong> {selectedName[2]}
                      </div>
                      <div className={`text-center py-2 rounded-md my-4 text-white ${getBgColor(selectedName[3])}`}>
                        <strong>Lunch:</strong> {selectedName[3]}
                      </div>
                      <div className={`text-center py-2 rounded-md my-4 text-white ${getBgColor(selectedName[4])}`}>
                        <strong>Dinner:</strong> {selectedName[4]}
                      </div>
                      <div className='text-center py-2 bg-white rounded-md my-4 '>
                        <strong>Preference: {selectedName[5]}</strong>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            )}
          </motion.div>
        )}
      </AnimatePresence>
      {!isLoading && (
        <div>
          <div className='mx-10 flex flex-col items-center'>
            <button
              onClick={handleSignOut}
              className="bg-gray-500 text-white rounded-md px-4 py-2"
            >
              Sign Out
            </button>
          </div>
          <footer className='text-center mb-10 text-gray-400 font-bold py-4'>
            Developed by <a href='https://bineet.dev'>Bineet</a>
          </footer>
        </div>
      )}
    </div>
  );
};

export default App;
